    --blue-color: #06f;
    --high-color: rgb(0, 45, 113);
    --med-color: rgb(0, 94, 235);
    --low-color: rgb(104, 164, 255);
    --sidebar-font-size: 1.2rem;
    --sidebar-font-weight: 500;
}

html,
body {
    height: 100%;
}

html {
    font-size: 16px;
}

body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: flex;
    flex-direction: column;
    margin: 0;
}

header {
    display: flex;
    position: sticky;
    top: 0;
    width: 100%;
    background-color: white;
    border-bottom: solid 0.5px rgb(160, 160, 160);
    box-shadow: 0 0rem 0.5rem black;
}

h1 {
    display: flex;
    align-items: center;
    margin: 0.3rem 1rem;
}

h1:before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    display: inline-block;
    filter: invert(26%) sepia(94%) saturate(3346%) hue-rotate(210deg) brightness(99%) contrast(107%);
    height: auto;
    margin: 0.3rem 0rem 0 0;
    width: 3rem;
}

h2 {
    font-size: 1.5rem;
    margin: 0.5rem 1rem;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

button {
    cursor: pointer;
    font-family: inherit;
    font-size: 100%;
}

textarea {
    resize: none;
}

input:not([type="checkbox"]),
textarea {
    appearance: none;
}

input:not([type="checkbox"]),
textarea,
select {
    border: solid 0.5px rgb(160, 160, 160);
    border-radius: 0.2rem;
    box-sizing: border-box;
    font-family: inherit;
    font-size: 100%;
    padding: .25rem .5rem;
}

input:not([type="checkbox"]):focus,
textarea:focus,
select:focus {
    outline: none;
    border: solid 2px var(--blue-color);
}

#content-container {
    display: grid;
    grid-template-columns: 1fr 4fr;
    height: 100%;
    overflow: hidden;
}

#sidebar {
    border-right: solid 0.5px rgb(160, 160, 160);
    box-shadow: 0 0rem 0.5rem black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1rem;
    min-width: 165px;
    overflow: auto;
    padding-bottom: 1rem;
}

#sidebar-nav-items {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    height: 100%;
    padding-top: 1rem;
    overflow: auto;
}

.sidebar-item,
.add-sidebar-item {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    font-size: var(--sidebar-font-size);
    font-weight: var(--sidebar-font-weight);
    gap: 1rem;
    height: 2.5rem;
    padding: 0 1rem;
    text-wrap: nowrap;
    transition: 0.3s;
    width: 100%;
}

.sidebar-item:hover {
    background-color: rgb(147, 190, 255);
    cursor: pointer;
}

.project {
    justify-content: space-between;
}

#proj-name {
    width: 100%;
}

.btn-grp {
    display: flex;
    gap: 0.2rem;
}

.icon-btn {
    border: none;
    height: 1.3rem;
    opacity: 0.35;
    width: 1.3rem;
}

.icon-btn:hover {
    opacity: 1.0;
}

.edit {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat center;
}

.delete {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) no-repeat center;
}

.confirm {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_3___}) no-repeat center;
}

.cancel {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_4___}) no-repeat center;
}

.edit:hover,
.confirm:hover {
    filter: invert(34%) sepia(100%) saturate(1030%) hue-rotate(89deg) brightness(98%) contrast(107%);
}

.delete:hover,
.cancel:hover {
    filter: invert(25%) sepia(100%) saturate(7194%) hue-rotate(357deg) brightness(92%) contrast(119%);
}

#new-project-btn {
    align-items: center;
    align-self: center;
    background-color: var(--blue-color);
    border: none;
    border-radius: 0.5rem;
    color: white;
    display: flex;
    font-size: 1.2rem;
    font-weight: 600;
    gap: 0.3rem;
    justify-content: center;
    padding: 0.2rem 1rem;
    transition: 0.3s;
    width: 90%;
}

#new-project-btn:hover {
    box-shadow: 0rem 0rem 0.5rem var(--blue-color);
    opacity: 0.9;
}

#new-project-btn span {
    font-size: 2rem;
    margin-bottom: 0.2rem;
}

#tasks {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-height: 100%;
    overflow: auto;
    padding: 1.5rem 1rem;
}

.task {
    align-items: center;
    display: flex;
    gap: 1rem;
}

.task-box {
    align-items: center;
    border: solid 0.5px black;
    border-radius: 0.5rem;
    display: flex;
    flex: 1 1 100%;
    gap: 1rem;
    padding: 0.5rem 1.5rem;
}

.high-priority {
    background-image: linear-gradient(to right, var(--high-color), var(--high-color) 1rem, transparent 1rem, transparent 100%);
}

.med-priority {
    background-image: linear-gradient(to right, var(--med-color), var(--med-color) 1rem, transparent 1rem, transparent 100%);
}

.low-priority {
    background-image: linear-gradient(to right, var(--low-color), var(--low-color) 1rem, transparent 1rem, transparent 100%);
}

.complete {
    background-color: rgba(129, 129, 129, 0.5);
    opacity: 0.5;
    text-decoration: line-through;
}

.task-name {
    font-weight: 500;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.task-due {
    font-size: 0.8rem;
    font-style: italic;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.edit-task-box {
    border: solid 0.5px black;
    border-radius: 0.5rem;
    display: grid;
    gap: 1rem;
    grid-template: 1fr 2fr / 1fr 1fr 1fr 1fr;
    overflow: auto;
    padding: 1rem;
    width: 100%;
}

#task-name {
    grid-column: 1;
}

#task-due {
    grid-column: 2;
}

#task-priority {
    grid-column: 3;
}

#task-project {
    grid-column: 4;
}

#task-desc {
    grid-column: 1 / 5;
    grid-row: 2;
}

#new-task-btn {
    background-color: var(--blue-color);
    border: none;
    border-radius: 50%;
    bottom: 1rem;
    color: white;
    font-size: 2rem;
    height: 3rem;
    padding-bottom: 0.45rem;
    position: fixed;
    right: 1rem;
    transition: 0.3s;
    width: 3rem;
}

#new-task-btn:hover {
    box-shadow: 0rem 0rem 0.5rem var(--blue-color);
    opacity: 0.9;
